nav {
   position: fixed;
   width: 100%;
   height: 150px;
   display: grid;
   grid-template-columns: 0.5fr 1fr 1fr 1fr 1fr 0.5fr;
   column-gap: 25px;
   margin-top: 2%;
}

.main-logo {
   height: 150px;
   grid-column: 2;
}

nav h1 {
   font-size: 30px;
   align-self: center;
   background-color: brown;
   width: 250px;
   text-align: center;
   padding: 9px 15px 15px 15px;
   border-radius: 20px;
}